import React from 'react';
import { TbWorldWww } from 'react-icons/tb';
import { FaGithub, FaFacebookF, FaInstagram, FaRedditAlien, FaDiscord, FaLinkedinIn, FaApple, FaGooglePlay } from 'react-icons/fa';
import { IoLogoTiktok, IoLogoYoutube } from 'react-icons/io5';
import { BiLogoTelegram } from 'react-icons/bi';
import { FaXTwitter } from 'react-icons/fa6';

const iconMap = {
  website: { component: <TbWorldWww className='text-gray-600 h-12 w-12' />, placeholder: 'www.yoursite.com', label: 'Website', link: '' },
  github: { component: <FaGithub className='text-gray-600 h-12 w-12' />, placeholder: 'username', label: 'GitHub', link: 'https://www.github.com/' },
  facebook: { component: <FaFacebookF className='text-gray-600 h-12 w-12' />, placeholder: 'username', label: 'Facebook', link: 'https://www.facebook.com/' },
  instagram: { component: <FaInstagram className='text-gray-600 h-12 w-12' />, placeholder: 'username', label: 'Instagram', link: 'https://www.instagram.com/' },
  twitter: { component: <FaXTwitter className='text-gray-600 h-12 w-12' />, placeholder: 'username', label: 'Twitter', link: 'https://www.twitter.com/' },
  tiktok: { component: <IoLogoTiktok className='text-gray-600 h-12 w-12' />, placeholder: 'username', label: 'TikTok', link: 'https://www.tiktok.com/' },
  telegram: { component: <BiLogoTelegram className='text-gray-600 h-12 w-12' />, placeholder: 'username', label: 'Telegram', link: 'https://www.telegram.com/' },
  youtube: { component: <IoLogoYoutube className='text-gray-600 h-12 w-12' />, placeholder: 'username', label: 'YouTube', link: 'https://www.youtube.com/channel/' },
  reddit: { component: <FaRedditAlien className='text-gray-600 h-12 w-12' />, placeholder: 'username', label: 'Reddit', link: 'https://www.reddit.com/user/' },
  discord: { component: <FaDiscord className='text-gray-600 h-12 w-12' />, placeholder: 'username', label: 'Discord', link: 'https://www.discord.com/invite/' },
  linkedin: { component: <FaLinkedinIn className='text-gray-600 h-12 w-12' />, placeholder: 'username', label: 'LinkedIn', link: 'https://www.linkedin.com/company/' },
  appstore: { component: <FaApple className='text-gray-600 h-12 w-12' />, placeholder: 'www.apple.com/app-store', label: 'App Store', link: 'https://www.apple.com/app-store/' },
  playstore: { component: <FaGooglePlay className='text-gray-600 h-12 w-12' />, placeholder: 'play.google.com/store/apps/details?id=com.username', label: 'Play Store', link: 'https://play.google.com/store/apps/details?id=' },
};

const Links = ({ project }) => {
  const links = project?.links;
  let count = 0;
  return (
    <div className='flex flex-wrap gap-4 mt-8'>
      {
      Object.keys(iconMap)?.map((type) => {
        links[type] && count++;
        return(
        links[type] && 
        (
          <a
            key={type}
            href={type === 'website' ? links[type] : `${iconMap[type].link}${links[type]}`}
            target="_blank"
            rel="noopener noreferrer"
            className='bg-gray-100 rounded-lg p-2 md:p-4 hover:bg-blue-100'
          >
            {iconMap[type].component}
          </a>
        )
      )})
    }
    {count===0 && <p>No Links found</p>}
    </div>
  );
};

export default Links;
