import React from 'react';
import { Timeline } from 'react-twitter-widgets';

const TwitterFeed = ({ username }) => {
  return (
    <div className="twitter-feed-container mt-8">
      {username ? (
        <Timeline
          dataSource={{
            sourceType: 'profile',
            screenName: username
          }}
          options={{
            width: '100%',
            height: '600'
          }}
        />
      ) : (
        <h2>No Twitter username found</h2>
      )}
    </div>
  );
};

export default TwitterFeed;
