import React, { useContext, useEffect, useState } from 'react';
import { FaShareSquare } from 'react-icons/fa';
import { RWebShare } from "react-web-share";
import { BiSolidUpArrow } from 'react-icons/bi';
import { Links, OverView, TwitterFeed, YoutubeEmbed } from '../components/profiledetails';
import { useLocation } from '@reach/router';
import { ProjectContext } from '../context/ProjectContext';
import Layout from '../components/layout';

const ProfileDetail = () => {
  const [activeSection, setActiveSection] = useState('overview');
  const location = useLocation();
  const params = new URLSearchParams(location.search);
  const project_Id = params.get('projectId') || '';
  const { project, handleGetOneProject, resetStates } = useContext(ProjectContext);
  const [loading, setLoading] = useState(true);

  const { handleUpvoteProject } = useContext(ProjectContext)
  const [upvotes, setUpvotes] = useState(project.upvotes);
  const handleSetUpvotes = () => {
    handleUpvoteProject(project_Id);
    setUpvotes(upvotes+1);
  }

  useEffect(() => {
    handleGetOneProject(project_Id)
  }, [project_Id]);

  useEffect(() => {
    if (project.name !== '') {
      setUpvotes(project.upvotes);
      setLoading(false);
    }
  }, [project]);

  useEffect(() => {
    setLoading(true);
    resetStates();
  }, []);

  return (
    <Layout>
    {
      loading ? (
        <div className="flex justify-center items-center mt-[10%]">
          <div className="spinner-border animate-spin inline-block w-8 h-8 border-4 rounded-full border-blue-500 border-t-transparent"></div>
        </div>
      ) :  (
        <div>
          <div className='h-80 w-full'>
            <img src={project?.coverImg} className='object-strech h-64 w-full' />
          </div>
          <div className='mx-4 mt-4'>
            <div className='flex flex-col md:flex-row'>
              <img src={project?.logo} className='h-20 w-28' />
              <div className='md:ml-2 mt-2 md:mt-0'>
                <h2 className='text-2xl font-bold'>{project?.name}</h2>
                <p>{project?.tldr}</p>
                <div className='flex space-x-2 mt-2'>
                  {project?.tags?.map((item, index) => <p key={index} className='px-2 rounded-sm border-[1px] bg-secondarylight text-xs'>{item.name}</p>)}
                  <RWebShare
                    data={{
                      text: project?.tldr,
                      url: window.location.href,
                      title: project?.name,
                    }}
                    onClick={() => console.log("shared successfully!")}
                  >
                    <button className="text-gray-500"><FaShareSquare /></button>
                  </RWebShare>
                </div>
              </div>
            </div>
            <div className='md:flex mt-12'>
              <div className='flex space-x-6 w-full md:w-auto border-b-2'>
                {['overview', 'links', 'videos', 'updates']?.map((section) => (
                  <p
                    key={section}
                    className={`font-bold cursor-pointer ${activeSection === section ? 'border-b-2 border-primarydark' : ''}`}
                    onClick={() => setActiveSection(section)}
                  >
                    {section.charAt(0).toUpperCase() + section.slice(1)}
                  </p>
                ))}
              </div>
              <div onClick={handleSetUpvotes} className='hidden md:w-24 ml-[20%] md:ml-12 mb-2 border-[1px] md:flex md:px-6 px-2 md:py-2 py-1 rounded-sm md:rounded-md text-gray-500 space-x-2'>
                <BiSolidUpArrow className='md:h-4 h-2 md:w-4 w-2 my-auto' />
                <p className='text-xs md:text-sm'>{upvotes}</p>
              </div>
            </div>
            {activeSection === 'overview' && (
              <div>
                <OverView project={project} />
                <Links project={project} />
                <YoutubeEmbed channelId={project?.links.youtube || ''} />
                <TwitterFeed username={project?.links.twitter || ''} />
              </div>
            )}
            {activeSection === 'links' && <Links project={project} />}
            {activeSection === 'videos' && <YoutubeEmbed channelId={project?.links.youtube || ''} />}
            {activeSection === 'updates' && <TwitterFeed username={project?.links.twitter || ''} />}
          </div>
        </div>
      )}
    </Layout>
  );
};

export default ProfileDetail;
