import React, { useState, useEffect } from 'react';
import axios from 'axios';

const YoutubeEmbed = ({ channelId }) => {
  const [videos, setVideos] = useState([]);
  const apiKey = 'AIzaSyDqYlCdQnNrjZOQhDdwNSR7GOUdQchxuUg';

  useEffect(() => {
    const fetchVideos = async () => {
      try {
        const response = await axios.get(
          `https://www.googleapis.com/youtube/v3/search`,
          {
            params: {
              part: 'snippet',
              channelId: channelId,
              maxResults: 4,
              order: 'date',
              type: 'video',
              key: apiKey
            }
          }
        );
        setVideos(response.data.items);
      } catch (error) {
        console.error('Error fetching YouTube videos', error);
      }
    };

    fetchVideos();
  }, [channelId, apiKey]);

  return (
    <div className="youtube-embed mt-8 grid gap-4 grid-cols-1 md:grid-cols-2 lg:grid-cols-4">
      { channelId ? (
        videos?.map((video) => (
          <div key={video.id.videoId} className="video-item flex flex-col border-b-2 p-4">
            <iframe
              className="w-full rounded-md"
              height="180px"
              src={`https://www.youtube.com/embed/${video.id.videoId}`}
              allowFullScreen
              title={video.snippet.title}
            ></iframe>
            <div className='flex flex-col mt-4'>
              <h3 className='font-extrabold text-lg'>{video.snippet.title}</h3>
              <p className='text-gray-400 font-semibold'>{video.snippet.channelTitle}</p>
            </div>
          </div>
        ))
      ) : (
        <p>No youtube channel id found</p>
      )}
    </div>
  );
};

export default YoutubeEmbed;
